import { Component, Input, OnInit } from '@angular/core';
import { MoviesapiService } from '../moviesapi.service';

@Component({
  selector: 'app-showall',
  templateUrl: './showall.component.html',
  styleUrls: ['./showall.component.css']
})
export class ShowallComponent implements OnInit {

  title;
  showspinner:boolean = true;
  @Input() showid;

  MovieList:any;
  constructor(public moviesapiservice:MoviesapiService) { }

  ngOnInit(){
    this.moviesapiservice.getShows().subscribe(data =>  {
      this.MovieList = data;
      this.showspinner=false;
      //localStorage['fff'] = JSON.stringify(data);
      // console.log(this.MovieList);
      //this.moviesapiservice = JSON.parse(localStorage['fff']);
    });
   
   
  }

  Showbyid(m){
    this.showid=m;
   
  }

}
